.img {
    height: 45px;
    padding: 10px 20px;
    transition: 1s all ease;
}

.img:hover{
    animation: img .5s alternate ease-in;
}

@keyframes img{
    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.3);
    }
    100%{
        transform: scale(1);
    }
}

.redes {
    margin-top: 10px;
    float: right;
    width: 200px;
    display: flex;
    flex-direction: 'row';
}


  
  